@tailwind base;
@tailwind components;
@tailwind utilities;

html {
    scroll-behavior: smooth;
  }
  

@font-face {
    font-family: 'CabinetGrotesk';
    src: url('assets/fonts/CabinetGrotesk-Black.otf') format('opentype');
    font-weight: 900; /* Black weight */
    font-style: normal;
}

@font-face {
    font-family: 'CabinetGrotesk';
    src: url('assets/fonts/CabinetGrotesk-Regular.otf') format('opentype');
    font-weight: 400; /* Regular weight */
    font-style: normal;
}

@font-face {
    font-family: 'CabinetGrotesk';
    src: url('assets/fonts/CabinetGrotesk-Light.otf') format('opentype');
    font-weight: 300; /* Light weight */
    font-style: normal;
}


.container {
    @apply px-9;
}


.circle {
    max-width: 430px;
    position: relative;
    
}
#audience-circle, #business-circle {
    transform: translateX(5rem);
}
#audience-circle::before, #business-circle::before {
    content: '';
    position: absolute;
    width: 40%;
    height: 40%;
    border-radius: 100%;
    background-color: #E8472F;
    left:-24%;
    top:-14%;
    
}

#partnership-circle, #best-self-circle {
    transform: translateX(-5rem);
}

#partnership-circle::after, #best-self-circle::after {
    content: '';
    position: absolute;
    width: 40%;
    height: 40%;
    border-radius: 100%;
    background-color: #E8472F;
    right:-24%;
    top:-14%;
  }

/* Styles for expanded circle */
.circle.expanded {
    position: absolute; /* You may also use fixed depending on your layout */
    top: 10%;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 75%;
    z-index: 1; /* Ensures the expanded circle is above other content */
    max-width: 100%;
    border-radius: 0; 
    box-shadow: none;
  }

  .circle.expanded .start-content {
    padding-top: 100px;
  }
  
  .hide-pseudo .circle::before,
  .hide-pseudo .circle::after {
    display: none;
  }

  #circle-section.hide-pseudo #circles-container {
    @apply bg-burnt-orange;
  }

  #circle-section.hide-pseudo .circle:not(.expanded) {
    visibility: hidden;

  }

  #circle-section.hide-pseudo .circle {
    transform: none;
  }

  #circle-section .open {
    display: none;
  }
  #circle-section.hide-pseudo .open {
    display: block;
  }
  #circle-section.hide-pseudo .closed {
    display: none;
  }

.circle, .circle::before, .circle::after {
  box-shadow: 10px 10px 30px 5px rgba(0,0,0,0.4);
}
  @media only screen and (min-width: 640px) {
    .circle {
        transform: none !important;
        margin-left: auto;
        margin-right: auto;
        min-width: 430px;
        max-width: none;
        width: 39vw;
    }
  }
  

  @media only screen and (min-width: 1024px) {
    #partnership-circle::before, #best-self-circle::before {
      content: '';
      position: absolute;
      width: 60%;
      height: 60%;
      border-radius: 100%;
      background-color: #E8472F;
      left: -42%;
      top: -32%;
    }
  }

  /* Application Form */
  #application-form input {
    @apply appearance-none block w-full bg-transparent border-white border rounded-3xl text-white font-roboto-mono text-center text-[10px] py-2 mb-4 placeholder-white leading-none;
  }
  #application-form input[type="submit"] {
    @apply bg-white border-white border rounded-3xl text-forest-green lg:col-span-2;
  }

  #application-form-container {
    @apply lg:grid lg:grid-cols-2 lg:gap-x-6;
  }
  #application-form-container .submit-button {
    @apply lg:col-span-2 lg:w-[50%] lg:mx-auto;
  }
  .wpcf7-spinner {
    @apply hidden;
  }
  .text-copy p {
    @apply mb-3;
  }

  .close-btn {
    position: absolute;
    top: -45px;
    right: 50px;
    cursor: pointer;
  }

  @media only screen and (max-width: 1024px) {
    .close-btn {
      top: 0;
    }
  }

  .intro-text:last-of-type span::after {
    content: '';
    display: inline-block;
    height: 18px;
    width: 18px;  
    background-image: url(assets/images/wheel.svg);
    right: 0;
    bottom: 0;
  }

  #circle-section {
	overflow-x: clip;
}